import React from 'react'

const AboutUs = () => {
    return (
        <div>
            <div className="about-us">
                <h4>Witamy na stronie sali prób oraz studia nagrań Próbówka - Music House Wrocław przy ulicy Robotniczej.  </h4>
                <p>Dlaczego warto wybrać jedną z naszych dwóch sal prób?
                    Sala prób Próbówka Music House to miejsce stworzone przez muzyków dla muzyków. Znamy wasze potrzeby. Miejsce jest klimatyzowane, ogrzewane, dostępne 24h na dobę, monitorowane, wyposażone w sprawdzony i zawsze działający sprzęt. Sala prób dysponuje także dużą ilością bezpłatnych miejsc parkingowych oraz toaletą. Oferujemy także możliwość bezpłatnego przechowywania swoich wzmacniaczy. 
                    Ponadto wraz z salami prób połączone jest profesjonalne studio nagrań. U nas bez problemu zarejestrujesz swoją próbę, nagrasz swój występ czy utrwalisz materiał na planowaną płytę. </p>
            </div>
        </div>
    )
}

export default AboutUs
